:host {
    --primary: #4e54c8;
    --primary-light: #8f94fb;
    --primary-dark: #363795;
    --accent: #4caf50;
    --accent-light: #81c784;
    --text-primary: #333;
    --text-secondary: #666;
    --background-light: #f5f7fa;
    --card-bg: #ffffff;
    --success: #00c853;
    --warning: #ffd600;
  }

  .dashboard-container {
    height: 100vh;
    display: flex;
    flex-direction: column;
  }

  .toolbar {
    display: flex;
    justify-content: space-between;
    padding: 0 16px;
    background: linear-gradient(135deg, var(--primary) 0%, var(--primary-dark) 100%);
    color: white;
    box-shadow: 0 2px 6px rgba(0, 0, 0, 0.15);
  }

  .toolbar-left {
    display: flex;
    align-items: center;
    gap: 16px;
  }

  .toolbar-right {
    display: flex;
    align-items: center;
    gap: 8px;
  }

  .app-title {
    font-size: 1.25rem;
    font-weight: 600;
    letter-spacing: 0.5px;
  }

  /* Typewriter animation */
  .typewriter {
    overflow: hidden;
    border-right: 3px solid rgba(255,255,255,0.75);
    white-space: nowrap;
    margin: 0;
    animation: 
      typing 3.5s steps(30, end),
      blink-caret 0.75s step-end infinite;
  }

  @keyframes typing {
    from { width: 0 }
    to { width: 100% }
  }

  @keyframes blink-caret {
    from, to { border-color: transparent }
    50% { border-color: rgba(255,255,255,0.75) }
  }

  .sidenav-container {
    flex: 1;
    background-color: var(--background-light);
  }

  .sidenav {
    width: 260px;
    background-color: var(--card-bg);
    border-right: 1px solid var(--background-light);
  }

  .sidenav-header {
    padding: 24px 16px;
    background-color: var(--primary-dark);
    color: white;
  }

  .logo-container {
    display: flex;
    align-items: center;
    gap: 10px;
  }

  .logo-icon {
    font-size: 28px;
    height: 28px;
    width: 28px;
  }

  .logo-text {
    font-size: 1.25rem;
    font-weight: 600;
    letter-spacing: 0.5px;
  }

  .content {
    background-color: var(--background-light);
  }

  .content-wrapper {
    padding: 24px;
    max-width: 1200px;
    margin: 0 auto;
  }

  mat-nav-list {
    padding-top: 16px;
  }

  mat-nav-list a {
    height: 48px;
    margin: 8px 12px;
    border-radius: 8px;
    color: var(--text-secondary);
    font-weight: 500;
    transition: all 0.3s ease;
  }

  mat-nav-list a.active {
    background-color: var(--primary-light);
    color: var(--primary);
    transform: translateX(5px);
    position: relative;
  }

  mat-nav-list a.active::before {
    content: '';
    position: absolute;
    left: 0;
    top: 0;
    height: 100%;
    width: 4px;
    background: var(--accent);
    border-radius: 0 4px 4px 0;
  }

  mat-nav-list a:hover {
    background-color: var(--background-light);
    transform: translateX(5px);
  }

  .nav-item {
    display: flex;
    align-items: center;
  }

  .nav-item i {
    margin-right: 12px;
    color: var(--text-secondary);
    transition: transform 0.3s ease, color 0.3s ease;
    font-size: 20px;
    width: 20px;
    text-align: center;
  }

  .nav-item.active i {
    color: var(--primary);
  }

  .nav-item:hover i {
    transform: scale(1.1);
    color: var(--primary-light);
  }

  .toolbar-right i {
    font-size: 20px;
    color: white;
  }

  @media (max-width: 768px) {
    .sidenav {
      width: 240px;
    }

    .content-wrapper {
      padding: 16px;
    }
  }

  .nav-section {
    padding: 8px 0;
    transition: opacity 0.5s ease;
  }

  h3[matSubheader] {
    color: var(--text-secondary);
    font-size: 0.75rem;
    font-weight: 600;
    text-transform: uppercase;
    letter-spacing: 0.05em;
    padding: 16px 24px 8px;
    margin: 0;
  }

  mat-divider {
    margin: 8px 0;
    background-color: var(--background-light);
  }

  .mat-badge-content {
    font-family: 'Inter', sans-serif;
    font-weight: 500;
    background-color: var(--accent) !important;
  }

  /* Custom menu styles */
  ::ng-deep .custom-menu {
    background-color: white;
  }

  ::ng-deep .custom-menu .mat-menu-item:hover {
    background-color: var(--background-light);
  }

  ::ng-deep .custom-menu .mat-menu-item i {
    color: var(--primary);
    margin-right: 8px;
  }

  /* Animation for active items */
  .nav-item.active::after {
    content: '';
    position: absolute;
    right: 16px;
    top: 50%;
    transform: translateY(-50%);
    width: 8px;
    height: 8px;
    border-radius: 50%;
    background-color: var(--accent);
    box-shadow: 0 0 8px var(--accent);
    animation: pulse 1.5s infinite;
  }

  @keyframes pulse {
    0% {
      box-shadow: 0 0 0 0 rgba(76, 175, 80, 0.7);
    }
    70% {
      box-shadow: 0 0 0 10px rgba(76, 175, 80, 0);
    }
    100% {
      box-shadow: 0 0 0 0 rgba(76, 175, 80, 0);
    }
  }